@use "src/variables.scss" as var;

.home {
    display: flex;
    flex-wrap: nowrap;

    .section {
        min-width: 100vw;
        height: calc(100vh - var.$navbar-height);
        overflow-y: scroll;
        scroll-snap-align: start;
    }
}

@media screen and (min-width: 800px){
    .home {
        display: block;
        flex-wrap: wrap;
        .section {
            border-style: solid;
            border-color: grey;
            border-width: 1px;
            height: fit-content;
            min-width: 800px;
            max-width: 800px;
            margin: auto;
        }
    }
    
}